import Endpoint from '@/services/Endpoint'

export default {
  transferPurposes (payload) {
    return Endpoint().get('/s1/ft/fundsTransferPurpose', {
      params: payload
    })
  },
  createTransferPurpose (payload) {
    return Endpoint().post('/s1/ft/fundsTransferPurpose', payload)
  },
  modifyTransferPurpose (payload) {
    return Endpoint().patch('/s1/ft/fundsTransferPurpose', payload)
  },
  transferPurposeTree (payload) {
    return Endpoint().get('/s1/ft/fundsTransferPurpose/tree', payload)
  }
}
