<template id="popup">
  <vs-popup fullscreen :title="title" :active.sync="activeInternal">
    <div :ref="popupRef" class="vs-con-loading__container">
      <vs-table class="mb-base" :data="transferPurposeTableData" :noDataText="transferPurposeTableNoDataText" :sst="true" search @search="filterTransferPurposeTable" @sort="orderTransferPurposeTable" @selected="selectRow">
        <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
          <div class="flex flex-wrap-reverse items-center">
            <vs-button class="p-3 mr-4" @click="loadTransferPurposeTableData(popupRef)" type="filled" icon="fa-sync" icon-pack="far" :disabled="transferPurposeTableBusy">{{ $t('REFRESH') }}</vs-button>
          </div>

          <div class="flex justify-end mr-4">
            <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
              <span class="mx-2">{{ transferPurposeTablePagingInfo }}</span>
              <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
                <span class="far fa-angle-down text-primary"></span>
                <vs-dropdown-menu>
                  <vs-dropdown-item :key="index" v-for="(item, index) in tablePageOptions" @click="transferPurposeTablePerPage = item">
                    <span>{{ item }}</span>
                  </vs-dropdown-item>
                </vs-dropdown-menu>
              </vs-dropdown>
            </div>
          </div>
        </div>

        <template slot="thead">
          <vs-th sort-key="purposeId">
            {{ this.$t('ID') }}
          </vs-th>
          <vs-th sort-key="purposeName">
            {{ this.$t('PURPOSE_NAME') }}
          </vs-th>
          <vs-th sort-key="externalPurposeId">
            {{ this.$t('EXTERNAL_ID') }}
          </vs-th>
          <vs-th sort-key="position">
            {{ this.$t('POSITION') }}
          </vs-th>
        </template>

        <template slot-scope="{data}">
          <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
            <vs-td :data="data[index].purposeId">
              {{ data[index].purposeId }}
            </vs-td>
            <vs-td :data="data[index].purposeName">
              {{ data[index].purposeName | placeholder('-') }}
            </vs-td>
            <vs-td :data="data[index].externalPurposeId">
              {{ data[index].externalPurposeId | placeholder('-') }}
            </vs-td>
            <vs-td :data="data[index].position">
              {{ data[index].position | placeholder('-') }}
            </vs-td>
          </vs-tr>
        </template>
      </vs-table>

      <vs-pagination :total="transferPurposeTableTotalPages" v-model="transferPurposeTableCurrentPage" :max="maxPaginationItems"></vs-pagination>
    </div>
  </vs-popup>
</template>

<script>
import PopupMixin from '@/mixins/commons/PopupMixin'
import FundsTransferPurposeTableMixin from '@/mixins/ft/FundsTransferPurposeTableMixin'

export default {
  name: 'funds-transfer-purpose-popup',
  mixins: [PopupMixin, FundsTransferPurposeTableMixin],
  mounted: function () {
    this.loadTransferPurposeTableData(this.popupRef)
  },
  beforeDestroy: function () {
    this.$el.remove()
  }
}
</script>
