import FundsTransferPurposeService from '@/services/ft/FundsTransferPurposeService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let TransferPurposeTableMixin = {
  mixins: [LoadingMixin, ListLayoutMixin],
  props: {
    defaultTransferPurposeTableFilter: {
      type: String,
      default: null
    },
    defaultTransferPurposeTableOrderBy: {
      type: String,
      default: null
    }
  },
  data: function () {
    return {
      transferPurposeTableData: [],
      transferPurposeTableFilterTimer: null,
      transferPurposeTableFilter: this.defaultTransferPurposeTableFilter,
      transferPurposeTableOrderBy: this.defaultTransferPurposeTableOrderBy,
      transferPurposeTableContainer: 'loading-container',
      transferPurposeTableBusy: false,
      transferPurposeTableTotalRows: 0,
      transferPurposeTableCurrentPage: 1,
      transferPurposeTablePerPage: 10
    }
  },
  computed: {
    transferPurposeTableNoDataText: function () {
      return this.transferPurposeTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    transferPurposeTableTotalPages: function () {
      return Math.ceil(this.transferPurposeTableTotalRows / this.transferPurposeTablePerPage)
    },
    transferPurposeTablePagingInfo: function () {
      return this.pagingInfo(this.transferPurposeTableCurrentPage, this.transferPurposeTablePerPage, this.transferPurposeTableTotalRows)
    }
  },
  methods: {
    loadTransferPurposeTableData (loadingContainerId) {
      let self = this
      this.transferPurposeTableBusy = true
      this.transferPurposeTableContainer = loadingContainerId || this.transferPurposeTableContainer
      this.startLoading(this.transferPurposeTableContainer)
      FundsTransferPurposeService.transferPurposes({
        pageIndex: this.transferPurposeTableCurrentPage - 1,
        pageSize: this.transferPurposeTablePerPage,
        filter: this.transferPurposeTableFilter,
        orderByField: this.transferPurposeTableOrderBy,
        ignoreTransferCurrency: true
      }).then(function (response) {
        self.stopLoading(self.transferPurposeTableContainer)
        self.transferPurposeTableBusy = false
        self.transferPurposeTableTotalRows = response.data.totalRows
        self.transferPurposeTableData = response.data.transferPurposeList || []
      }).catch(function (error) {
        self.stopLoading(self.transferPurposeTableContainer)
        self.transferPurposeTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshTransferPurposeTable () {
      this.loadTransferPurposeTableData()
    },
    filterTransferPurposeTable (filter) {
      let self = this
      if (this.transferPurposeTableFilter !== filter) {
        clearTimeout(this.transferPurposeTableFilterTimer)
        this.transferPurposeTableFilterTimer = setTimeout(() => {
          self.transferPurposeTableFilter = filter
          self.loadTransferPurposeTableData()
        }, process.env.VUE_APP_SEARCH_DELAY)
      } else {
        clearTimeout(this.transferPurposeTableFilterTimer)
      }
    },
    orderTransferPurposeTable (orderBy, active) {
      this.transferPurposeTableOrderBy = (active ? '' : '-') + orderBy
      this.loadTransferPurposeTableData()
    }
  },
  watch: {
    transferPurposeTableCurrentPage: function () {
      this.refreshTransferPurposeTable()
    },
    transferPurposeTablePerPage: function () {
      this.refreshTransferPurposeTable()
    }
  }
}

export default TransferPurposeTableMixin
