<template>
  <div>
    <currency-popup
      :title="title"
      :popupRef="popupRef"
      :active="popupActive"
      :defaultCurrencyTableFilter="defaultTableFilter"
      :lcyOnly="lcyOnly"
      :fcyOnly="fcyOnly"
      :disabled="disabled"
      @select-row="selectRow"
      v-on:close="popupActive = false"/>

    <vue-simple-suggest
      :ref="suggestRef"
      v-model="mutableRecordDisplay"
      :list="getList"
      :debounce="200"
      :nullable-select="true"
      :value-attribute="valueField"
      :display-attribute="displayField"
      @select="selectSuggestion">

      <div class="g">
        <nv-input-group>
          <vs-input class="w-full lookup-input" v-model="mutableRecordDisplay"/>
          <template slot="append">
            <div class="append-text btn-addon">
              <vs-button :disabled="!mutableRecordDisplay" @click="clear" color="primary">
                <i class="far fa-eraser"></i>
              </vs-button>
              <vs-button @click="popupActive = true" color="primary">
                <i class="far fa-search"></i>
              </vs-button>
            </div>
          </template>
        </nv-input-group>
      </div>

      <template slot="misc-item-above" slot-scope="{ suggestions, query }">
        <div class="misc-item">
          <span>{{ $t('SEARCHING_FOR') }} '{{ query }}'.</span>
        </div>

        <template v-if="suggestions.length > 0">
          <div class="misc-item">
            <span>{{ suggestions.length }} {{ $t('RESULTS_FOUND') }} ...</span>
          </div>
          <hr>
        </template>

        <div class="misc-item" v-else-if="!loading">
          <span>{{ $t('NO_RESULTS_FOUND') }}</span>
        </div>
      </template>

      <div slot="suggestion-item" slot-scope="scope" :title="scope.suggestion.description">
        <div class="text">
          <span v-html="boldenSuggestion(scope)"></span>
        </div>
      </div>

      <div class="misc-item" slot="misc-item-below" slot-scope="{ suggestions }" v-if="loading">
        <span>{{ $t('LOADING') }} ...</span>
        {{ suggestions }}
      </div>
    </vue-simple-suggest>
  </div>
</template>

<script>
import VueSimpleSuggest from 'vue-simple-suggest/dist/cjs'
import LookupMixin from '@/mixins/commons/LookupMixin'
import CurrencyPopup from '@/views/nucleus/currency/CurrencyPopup'
import UomService from '@/services/commons/UomService'

export default {
  name: 'currency-lookup',
  mixins: [LookupMixin],
  components: {
    VueSimpleSuggest,
    CurrencyPopup
  },
  props: {
    lcyOnly: {
      type: Boolean,
      default: null
    },
    fcyOnly: {
      type: Boolean,
      default: null
    },
    disabled: {
      type: String,
      default: null
    }
  },
  methods: {
    getList (inputValue) {
      let self = this
      self.loading = true
      return new Promise((resolve, reject) => {
        return UomService.uoms({
          pageIndex: 0,
          pageSize: 10,
          filter: inputValue,
          uomTypeEnumId: 'UT_CURRENCY_MEASURE',
          lcyOnly: this.lcyOnly,
          fcyOnly: this.fcyOnly,
          disabled: this.disabled
        }).then(function (response) {
          self.loading = false
          let result = response.data.uomList || []
          resolve(result)
        }).catch(function (error) {
          self.loading = false
          reject(error)
        })
      })
    }
  },
  watch: {
    fcyOnly: function () {
      this.clear()
    }
  }
}
</script>
